import React from "react";
import {
  Typography,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
} from "@material-ui/core";
import { Add, Minimize } from "@material-ui/icons";
import styled, { AnyStyledComponent } from "styled-components";
import { withTranslation, WithTranslation } from "react-i18next";

import FaqData from "../../data/faq.json";

interface IFaqContent {
  title: string;
  content: string;
}

interface IFaqItemsState {
  panel: string;
}
const DEFAULT_STATE: IFaqItemsState = {
  panel: "",
};

class FaqItems extends React.PureComponent<WithTranslation, IFaqItemsState> {
  constructor(props: WithTranslation) {
    super(props);
    this.state = DEFAULT_STATE;
  }

  getFaqContent = (): Array<IFaqContent> => {
    return FaqData[this.props.i18n.language];
  };

  handleExpansionChange = (panel: string) => {
    this.state.panel === panel
      ? this.setState({ panel: "" })
      : this.setState({ panel });
  };
  render = () => {
    return (
      <FaqListWrapper>
        {this.getFaqContent().map((faqItem, index) => {
          const panelKey: string = "panel" + index;
          const expanded = this.state.panel === panelKey;
          return (
            <StyledExpansionPanel
              key={panelKey}
              expanded={expanded}
              onChange={() => this.handleExpansionChange(panelKey)}
            >
              <StyledExpansionPanelSummary
                expandIcon={
                  expanded ? <StyledMinimizeIcon /> : <StyledAddIcon />
                }
              >
                <Typography variant="h5">{faqItem.title}</Typography>
              </StyledExpansionPanelSummary>
              <StyledExpansionPanelDetails>
                <Typography variant="body1">{faqItem.content}</Typography>
              </StyledExpansionPanelDetails>
            </StyledExpansionPanel>
          );
        })}
      </FaqListWrapper>
    );
  };
}

const StyledExpansionPanel: AnyStyledComponent = styled(ExpansionPanel)`
  &:before {
    display: none;
  }

  && {
    border-radius: 0 0 70px 0;
    box-shadow: none;
    margin-bottom: 2rem;

    &.Mui-expanded {
      box-shadow: 20px 15px 50px rgba(26, 11, 61, 0.25);
      margin-bottom: 2rem;
    }

    &:first-child {
      border-top-left-radius: 0px;
      border-top-right-radius: 0px;
    }

    &:last-child {
      border-bottom-left-radius: 0px;
      border-bottom-right-radius: 70px;
    }
  }
`;

const StyledExpansionPanelSummary: AnyStyledComponent = styled(
  ExpansionPanelSummary,
)`
  && {
    font-weight: 600;
    padding: 0.5rem 2.8rem;
    border-radius: 0 0 70px 0;
    color: #003269;
    background-color: #e1e1e1;

    &.Mui-expanded {
      color: #ffffff;
      background: #0a6eaa;
    }
  }
`;

const StyledExpansionPanelDetails: AnyStyledComponent = styled(
  ExpansionPanelDetails,
)`
  && {
    font-size: 0.9rem;
    padding: 1.2rem 2.8rem 0.5rem;
    border-radius: 0 0 70px 0;
    background-color: #ffffff;
  }
`;

const StyledAddIcon: AnyStyledComponent = styled(Add)`
  color: #0a6eaa;
`;
const StyledMinimizeIcon: AnyStyledComponent = styled(Minimize)`
  && {
    color: #ffffff;
    margin-top: -0.5rem;
  }
`;
const FaqListWrapper: AnyStyledComponent = styled.div`
  text-align: left;
`;

export default withTranslation()(FaqItems);
